@import '../variables/vars.scss';

#home-canvas {
    margin-top: 10%;
    justify-content: center;
    display: flex;
    align-items: center;
}

#home-image {
    width: 50%;
}

.home-intro {
    display: grid;
    align-content: center;
    min-height: 100%;
}

#home-gradient {
    border-radius: 1%;
    margin-top: 5%;
    margin-left: 5%;
    height: 90%;
    width: 90%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
}

#home-background {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: -1;
}

#home-greeting {
    width: 50%;

    & p:first-of-type {
        font-size: 3rem;
    }
   //for text gradients. not being used right now.
   /* background-clip: text;
    color: transparent;*/
}